.navcontainer{
	background-image: linear-gradient(#1a1a1a 80%, black); 
	height: 80px;
	width: 100vw;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	box-shadow: 0px 0px 15px 1px white;
}

.item{
	color: #ffde72;
	-webkit-flex-shrink: 0;
	        flex-shrink: 0;
	padding: 1px;
	margin-left: 20px;
	margin-top: 10px; 
}

h1.item {
	font-family: 'Neucha', cursive;
}

